@use 'base/variables' as v;
@use 'base/mixins' as m;


.previa_articulo {
    margin: 2rem auto;
    background-color: v.$blanco;
    border-radius: 1rem;

    @include m.tablet {
        display: flex;
        margin: 4rem auto;
    }

    picture {
        flex: 0.8;
    }

    .imagen_header {
        border-radius: 1rem 1rem 0 0;
        min-height: 100%;

        @include m.tablet {
            border-radius: 1rem 0 0 1rem;
        };
    }

    .seccion_sm {
        padding: 3rem 3rem 1rem 3rem;
        position: relative;
        flex: 1;

        @include m.tablet {
            padding: 5rem 5rem 3rem 5rem;
        };
    
        h3 {
            font-size: 1.5rem;
            text-align: left;
            line-height: 2.5rem;
            color: v.$azulOscuro;

            @include m.tablet {
                font-size: 2.5rem;
                line-height: 3.5rem;
            };
        }
    
        p {
            color: v.$azulDesaturado;

            @include m.tablet {
                font-size: 1.5rem;
                line-height: 2.5rem;
            };
        }
    }
}
