@use 'base/variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}

*, 
*::after, 
*::before {
    box-sizing: inherit;
}

body {
    min-height: 100%;
    font-size: 1.6rem;
    background-color: v.$azulClaro;
    font-family: v.$fuente_principal;
    overflow-x: hidden;
    margin: 0;
}

.contenedor_flex {
    display: flex;
    min-height: 100vh;
    align-items: center;
}

.contenedor {
    width: 90%;
    max-width: 900px;
    margin: 0 auto;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
}