@use 'base/variables' as v;
@use 'base/mixins' as m;

h1, 
h2, 
h3 {
    text-align: center;
    font-weight: v.$bold;
    margin: 0 0 calc(v.$separacion / 3) 0;
}

h1 {
    font-size: 3.4rem;
}

h2 {
    font-size: 3rem;
}

h3 {
    font-size: 2.8rem;
    text-align: center;
}

p {
    font-size: 1.3rem;
    line-height: 2rem;
}
