@use 'base/variables' as v;
@use 'base/mixins' as m;

.informacion {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 3rem;

    .imagen_avatar {
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
    }
}

.icono_share {
    z-index: 10;

    .bx {
        padding: .7rem;
        background-color: v.$azulClaro;
        border-radius: 50%;
    
        &.activo {
            background-color: v.$azulDesaturado;
            color: v.$blanco !important;
        }
        
        &:hover {
            color: v.$blanco !important;
            background-color: v.$azulDesaturado;
            cursor: pointer;
        }
    }
}

.informacion_perfil {
    display: flex;
    gap: 2rem;
    align-items: center;

    .info {
        .nombre {
            color: v.$azulOscuro;
            font-weight: v.$bold;
            margin: 0;
        }

        .fecha {
            margin: 0;
        }
    }
}

.contenedor_share-mobile {
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    width: 0%;
    position: absolute;
    background-color: v.$azulOscuro;
    left: 0;
    bottom: 0;
    border-radius: 0 0 1rem 1rem;
    padding: 1rem 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

    transition-property: visibility width opacity;
    transition-duration: 1000ms;
    transition-timing-function: ease;

    &.mostrar {
        width: 100%;
        visibility: visible;
        opacity: 1;
    }
}

.contenedor_share-desktop {
    .triangulo {
        width: 0; 
        height: 0;
        border: 1rem solid transparent;
        border-bottom-color: v.$azulOscuro;
        position: absolute;
        transform: rotate(180deg);
        bottom: -20px;
        right: 45%;
    }
    
    visibility: hidden;
    opacity: 0;
    position: absolute;
    background-color: v.$azulOscuro;
    padding: 0 3rem;
    border-radius: 1rem;
    bottom: 10rem;
    right: -5.5rem;

    transition-property: visibility width opacity;
    transition-duration: 500ms;
    transition-timing-function: ease;

    &.mostrar {
        visibility: visible;
        opacity: 1;
    }
}

.iconos_share {
    display: flex;
    gap: 2rem;
    align-items: center;

    p {
        letter-spacing: .4rem;
        color: v.$azulClaro;
    }

    .iconos {
        display: flex;
        gap: 2rem;

        img {
            width: 2rem;
            height: 2rem;

            &:hover {
                cursor: pointer;
            }
        }
    }
}